body {
  font-family: "Montserrat", sans-serif;
  background-color: #282C34;
}

a {
  background-color: rgb(44, 55, 63);
  padding: 5px 20px;
  border-radius: 26px;
  color: whitesmoke;
  text-decoration: none;
  font-size: 26px;
}

.container {
  max-width: 1600px;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  align-items: center;
}